<template>
    <div class="scales" id="scales">
        <svg class="svg-scales" id="svg_scales" width="200" height="200">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'Scales',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            // Defining the data and the drawing area size
            var gdp_data = [17,9,5,3,34];
            var area_width = 200;
            var area_height = 200;
            var left_margin = 10;
            var right_margin = 10;

            // Calculates the heightist GDP value
            var max_gdp = Math.max.apply(Math, gdp_data);
            // console.log(max_gdp);
            // Then create a Scale, using max_gdp to define its domain
            // Its ranges goes between the left and right margins of the drawing area
            var myScale = d3.scaleLinear()
            // var myScale = d3.scale
            //                 .linear()
                            .domain([0, max_gdp])
                            .range([0, area_width - left_margin - right_margin]);

            // Calculate the spacing between bars
            var spacing = area_height / (gdp_data.length + 1);

            // Draw the bars inside svg_scales
            var fda = d3.select("svg#svg_scales");
            fda.selectAll("rect")
                .data(gdp_data)
                .enter()
                .append("rect")
                .attr("y", function(d,i){
                    return spacing * (i+1);
                })
                .attr("x", left_margin)
                .attr("width", function(d){
                    return myScale(d);
                })
                .attr("height", "20")
                .style("fill", "blue")
                .style("stroke", "black")
                .style("stroke-width", 2);
        }
    }
</script>