<template>
    <div class="animated-d3" id="animated-d3">
        <svg class="svg-animated-d3" id="svg_animated_d3" width="200" height="200">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'AnimatedD3',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            // Define the gdp data as an array of arrays
            var gdp_data = [
                [10,15],
                [15,25],
                [25,30],
                [10,25]
            ]
            // Then set the variables defining the drawing area's
            // var area_width = 200;
            var area_height = 200;
            var left_margin = 10;
            // var right_margin = 10;

            // Calculate the heightist gdp value, and create a scale using it
            var max_gdp = Math.max.apply(Math, gdp_data.map(
                function(d){
                    return Math.max(d[0], d[1]);
                }
            ));
            var myScale = d3.scaleLinear()
                            .domain([0, max_gdp])
                            .range([0, area_height]);
            // Calculate the spacings between bars
            var spacing = area_height / (gdp_data.length + 1);
            

            // Draw the GDP bars
            var fda = d3.select("svg#svg_animated_d3");
            fda.selectAll("rect")
                .data(gdp_data)
                .enter()
                .append("rect")
                .attr("y", function(d,i){
                    return spacing * (i+1);
                })
                .attr("x", left_margin)
                .attr("width", function(d){
                    return myScale(d[0]);
                })
                .attr("height", "20")
                .style("fill", "grey")
                .style("stroke", "black")
                .style("stroke-width", 1)
                .on('click', function(){
                    d3.select(this)
                    .style("stroke-width", 1)
                    .style("fill", "blue")
                    .transition()
                    .delay(100)
                    .duration(500)
                    .attr("width", function(d){
                        return myScale(d[1]);
                    });
                });

            // var circle = d3.select("svg#svg_animated_d3")
            //                 .append("circle")
            //                 .attr("cx", 50)
            //                 .attr("cy", 50)
            //                 .attr("r", 40)
            //                 .style("stroke", "black")
            //                 .style("stroke-width", 1)
            //                 .style("fill", "yellow")

            // circle.on('click', function(){
            //     console.log('Someone has clicked the yellow circle!')
            // })
        }
    }
</script>