<template>
    <div class="dots-diagonal" id="dots_diagonal">

    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'DotsDiagonal',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            var distros = [
                { 'name': 'Debian', 'year': 1993, 'visits': 1820}, 
                { 'name': 'Ubuntu', 'year': 2004, 'visits': 1588}, 
                { 'name': 'Kubuntu', 'year': 2005, 'visits': 363}, 
                { 'name': 'Mint', 'year': 2006, 'visits': 3069},
                { 'name': 'Knoppix', 'year': 2000,'visits': 219},
            ];

            // var relations = [
            //     {'from': 0, 'to': 1}, 
            //     {'from': 1, 'to': 2}, 
            //     {'from': 2, 'to': 3}, 
            //     {'from': 0, 'to': 4},
            // ];
            var relations = [4,0,1,2,3];

            // The Width and Height of our SVG area
            var w = 600; 
            var h = 300;

            // Margin around our chart
            var margin = 40;

            // Let the Javascript append the SVG area into our page body
            var darea = d3.select("div#dots_diagonal")
                            .append("svg")
                            .attr("width", w)
                            .attr("height", h);

            var years_range = function(data){
                var y1 = d3.min(data, function(o){ return o.year });
                var y2 = d3.max(data, function(o){ return o.year });
                return [y1-1, y2];
            }
            var visits_range = function(data){
                var v1 = d3.min(data, function(o){ return o.visits });
                var v2 = d3.max(data, function(o){ return o.visits });
                return [v1, v2];
            }

            var xScale = d3.scaleLinear()
                            .domain(years_range(distros))
                            .range([margin, w - margin]);

            var yScale = d3.scaleLinear()
                            .domain(visits_range(distros))
                            .range([h-margin, margin]);

            // var gen = d3.diagonal()
            var gen = d3.line()
                        // .source(function(d){
                        .x(function(d){
                            // var x = xScale(distros[d.from].year);
                            // var y = yScale(distros[d.from].visits);
                            // return {'x': x, 'y': y};
                            return xScale(distros[d].year);
                        })
                        // .target(function(d){
                        .y(function(d){
                            // var x = xScale(distros[d.to].year);
                            // var y = yScale(distros[d.to].visits);
                            // return {'x': x, 'y': y};
                            return yScale(distros[d].visits);
                        }).curve(d3.curveBumpX);
            
            darea.selectAll("path") 
                .data(relations)
                .enter()
                .append("path")
                .attr("d", gen(relations)) 
                .attr("stroke", "#FF5599") 
                .attr("stroke-width", "10px") 
                .attr("fill", "none");

            darea.selectAll(".distrodot")
                .data(distros)
                .enter()
                .append("circle")
                .attr("class", "distrodot")
                .attr("cx", function(d){
                    return xScale(d.year);
                })
                .attr("cy", function(d){
                    return yScale(d.visits);
                })
                .attr("r", "26")
                .attr("stroke", "#000")
                .attr("stroke-width", "2px")
                .attr("fill", "#5599FF");

            darea.selectAll(".name-labels") 
                    .data(distros)
                    .enter()
                    .append("text") 
                    .style("text-anchor", "middle")
                    .attr("class", "name-labels") 
                    .attr("x", function(d){
                        return xScale(d['year']); 
                    })
                    .attr("y", function(d){
                        return yScale(d['visits']);
                    }) 
                    .text(function(d){
                        return "" + d['name'] + "";
                    });
        }
    }
</script>

<style>    
    .name-labels{
        font-family: sans-serif;
        font-size: 10px;
        stroke: #333;
    }
</style>