<template>
    <div class="basic-shapes" id="basic-shapes">
        <svg class="svg-basic-shapes" id="svg_basic_shapes" width="200" height="200">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'BasicShapes',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          

            var fda = d3.select("svg#svg_basic_shapes");
            var stroke_width = 2;
            var stroke_color = "black";

            // Drawing a Circle
            fda.append("circle")
                .attr("cx", 50)
                .attr("cy", 50)
                .attr("r", 40)
                .style("stroke", stroke_color)
                .style("stroke-width", stroke_width)
                .style("fill", "yellow");

            // Drawing a Square, i.e. rectangle with equal sides
            fda.append("rect")
                .attr("x", 100)
                .attr("y", 100)
                .attr("width", 50)
                .attr("height", 50)
                .style("stroke", stroke_color)
                .style("stroke-width", stroke_width)
                .style("fill", "red");            
        }
    }
</script>