<template>
    <div class="d3-chart" id="d3-chart">
        <svg class="svg-d3-chart" id="svg_d3_chart" width="200" height="200">
        </svg>
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'D3Chart',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            // Array of GDP values
            var gdp_data = [17,9,5,3];
            // The drawing area's width and height
            // var area_witdth = 200;
            var area_height = 200;
            // Calculating the spacing between bars.
            var spacing = area_height / (gdp_data.length + 1);

            // Select the drawing area and assing it to fda.
            var fda = d3.select("svg#svg_d3_chart");
            // Appending one rectangle for each item of gdp_data.
            fda.selectAll("rect")
                .data(gdp_data)
                .enter()
                .append("rect")
                .attr("y", function(d,i){
                    // i is the order of this item in gdp_data.
                    // Where first item is 0, second item is 1, etc.
                    return spacing * (i+1);
                })
                .attr("x", "10")
                .attr("width", function(d){
                    // Widths are proportional to values in gdp_data.
                    // d here is same as gdp_data[i]
                    return 10 * d;
                })
                .attr("height", "20")
                .style("fill", "blue")
                .style("stroke", "black")
                .style("stroke-width", 2)

            d3.select("svg#svg_d3_chart")
                .selectAll("circle")
                .data([40,80,120,160])
                .enter()
                .append("circle")
                .attr("cy", "40")
                .attr("cx", function(d){
                    return d;
                })
                .attr("r", "15")
                .style("fill", "red")
                .style("stroke", "black")
                .style("stroke-wdith", 2);
        }
    }
</script>