<template>
    <div class="arcs" id="arcs">

    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'Arcs',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            var gdp = [
                { 'country': 'Germany', 'color': '#2ca02c', 'value': 3 },
                { 'country': 'Japan', 'color': '#9467bd',  'value': 5 },
                { 'country': 'China', 'color': '#d62728', 'value': 9 },
                { 'country': 'USA', 'color': '#1f77b4', 'value': 17 },
            ];

            var w = 400;
            var h = 300;

            var svg = d3.select("div#arcs")
                        .append("svg")
                        .attr("width", w)
                        .attr("height", h);

            var max_gdp = d3.max(gdp, function(o){ return o.value });
            var angleScale = d3.scaleLinear()
                                .domain([0, max_gdp])
                                .range([0, 1.5 * Math.PI]);

            var arc = d3.arc() 
                        .innerRadius(function(d,i){
                            return (i+1)*25;
                        })
                        .outerRadius(function(d,i){ 
                            return (i+2)*25;
                        }) 
                        .startAngle(function(){
                            return angleScale(0);
                        })
                        .endAngle(function(d){
                            return angleScale(d.value);
                        });

            // Create a group and append it to the drawing area
            var g = svg.append("g");

            // Rather than adding shapes to the drawing area,
            // this time they are added to the newly created group, g.
            g.selectAll("path")
                .data(gdp)
                .enter()
                .append("path")
                .attr("d", arc)
                .attr("fill", function(d){
                    return d.color;
                })
                .attr("stroke", "#fff")
                .attr("stroke-width", "1px");

            // Text is appended to the group too. 
            g.selectAll("text")
                .data(gdp) 
                .enter() 
                .append("text") 
                .text(function(d){
                    return d.country + " ($" + d.value + " Trillion)"; 
                })
                .attr("x", function(){ 
                    return -10;
                })
                .attr("y", function(d,i){
                    return -(i+1)*25; 
                    })
                .attr("dy", -8) 
                .style("text-anchor", "end") 
                .style("font-size", "19px");

            // Moving the entire group including the shapes appended to it using SV transformation.
            g.attr("transform", "translate(" +  w/2 + "," + h/2 + ")");

        }
    }
</script>

