<template>
    <div class="area-chart" id="area_chart">
        <!-- <svg class="svg-area-chart" id="svg_area_chart" width="200" height="200" style="background: #eee">
        </svg> -->
    </div>
</template>

<script>
    import * as d3 from "d3"
    
    export default {
        name: 'AreaChart',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){          
            var germany_cup = [
                { 'year': 1990, 'goals_for': 15, 'goals_against': 5, 'position': 1 }, 
                { 'year': 1994, 'goals_for': 9, 'goals_against': 7, 'position': 5 }, 
                { 'year': 1998, 'goals_for': 8, 'goals_against': 6, 'position': 7 }, 
                { 'year': 2002, 'goals_for': 14, 'goals_against': 3, 'position': 2 }, 
                { 'year': 2006, 'goals_for': 14, 'goals_against': 6, 'position': 3 }, 
                { 'year': 2010, 'goals_for': 16, 'goals_against': 5, 'position': 3 }, 
                { 'year': 2014, 'goals_for': 18, 'goals_against': 4, 'position': 1 }
            ];

            // The Width and Height of our SVG area
            var w = 400; 
            var h = 300;

            // Margin around our chart
            var margin = 20;

            // Let the Javascript append the SVG area into our page body
            var svg_area = d3.select("div#area_chart")
                            .append("svg")
                            .attr("width", w)
                            .attr("height", h);

            var xScale = d3.scaleLinear()
                            // It is better to programmatically calculate
                            // the domain values from the data.
                            // Here, we do it manually for simplicity
                            .domain([1988, 2016])
                            .range([margin, w - margin]);

            var yScale = d3.scaleLinear()
                            // Again, the domain values from the data
                            // are calculated manually for simplicity
                            .domain([0, 20])
                            .range([h - margin, margin]);

            var gen = d3.area()
                        .x(function(d){
                            return xScale(d['year']);
                        })
                        .y0(function(d){
                            return yScale(d['goals_for'])
                        // }).interpolate('monotone');
                        })
                        .y1(function(d){
                            return yScale(d['goals_against'])
                        // }).interpolate('monotone');
                        }).curve(d3.curveMonotoneX);

            
            svg_area.append("path")
                    .attr("class", "area")
                    .attr("d", gen(germany_cup));

            svg_area.append("text")
                    .style("text-anchor", "middle")
                    .attr("x", w/2)
                    .attr("y", 20)
                    .text("Goals For/Against");
            
            svg_area.append("text")
                    .style("text-anchor", "middle")
                    .attr("x", w/2)
                    .attr("y", h-20)
                    .text("Germany World Cup");

            svg_area.selectAll(".year-labels") 
                    .data(germany_cup)
                    .enter()
                    .append("text") .attr("class", "year-labels") 
                    .attr("x", function(d){
                        return xScale(d['year']) - 10; 
                    })
                    .attr("y", function(d){
                        return yScale(d['goals_against']) + 20;
                    }) 
                    .text(function(d){
                        return "" + d['year'] + "";
                    });

            svg_area.selectAll(".team-positions") 
                    .data(germany_cup)
                    .enter()
                    .append("text") .attr("class", "team-positions") 
                    .attr("x", function(d){
                        return xScale(d['year']) - 10; 
                    })
                    .attr("y", function(d){
                        return yScale(d['goals_for']) - 20;
                    }) 
                    .text(function(d){
                        if(d['position'] == 1){
                            return "" + d['position'] + "st";
                        } else {
                            return "" + d['position'] + "th";
                        }                        
                    });

            // var yAxis = d3.svg.axis()
            var yAxis = d3.axisRight()
                            .scale(yScale)
                            // .orient("right");
                            .ticks(10);

            // svg_area.call(yAxis);

            svg_area.append("g")
                    .attr("transform", "translate(" + (w - margin) + ",0)")
                    .attr("class", "y axis")
                    .call(yAxis)
        }
    }
</script>

<style>
    .area{
        fill: #7F00FF;
        stroke: #bbb;
    }
    .year-labels, .team-positions{
        font-family: sans-serif;
        font-size: 8px;
        stroke: #555;
    }
    .axis path, 
    .axis line{
        fill: none;
        stroke: #bbb;
        shape-rendering: crispEdges;
    }
    .axis text{
        font-family: sans-serif;
        font-size: 10px;
        stroke: #333;
    }
</style>